<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <a-form-model-item label="角色组编码" prop="groupCode">
              <a-input
                v-model="entity.groupCode"
                placeholder="请输入角色组编码"
                :disabled="!!id"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="角色组名称" prop="groupName">
              <a-input
                v-model="entity.groupName"
                placeholder="请输入角色组名称"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="状态" prop="status">
              <a-select
                v-model="entity.status"
                placeholder="请选择状态"
              >
                <a-select-option value="启用">启用</a-select-option>
                <a-select-option value="禁用">禁用</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>

        <a-row :gutter="24">
          <a-col :sm="24">
            <a-form-model-item label="包含角色" prop="roles">
              <a-select
                v-model="entity.roles"
                mode="multiple"
                placeholder="请选择角色"
                style="width: 100%"
              >
                <a-select-option v-for="role in roleOptions" :key="role.value" :value="role.value">
                  {{ role.label }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>

        <a-row :gutter="24">
          <a-col :sm="24">
            <a-form-model-item label="描述" prop="description">
              <a-textarea
                v-model="entity.description"
                placeholder="请输入描述"
                :rows="4"
                :max-length="1000"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <g-collapse headerName="其他信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <a-form-model-item label="创建人">
                <span>{{ entity.creator || 'admin' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="创建时间">
                <span>{{ entity.createTime || '2024-01-01 12:00:00' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="最后修改人">
                <span>{{ entity.modifier || 'admin' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="最后修改时间">
                <span>{{ entity.modifyTime || '2024-01-01 12:00:00' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditESignRoleGroup",
  mixins: [GNdoPageEditBase],

  data() {
    return {
      entity: {
        groupCode: "",
        groupName: "",
        roles: [],
        status: "启用",
        description: "",
        creator: "",
        createTime: "",
        modifier: "",
        modifyTime: "",
      },
      roleOptions: [
        { value: "管理员", label: "管理员" },
        { value: "操作员", label: "操作员" },
        { value: "审核员", label: "审核员" },
        { value: "质检员", label: "质检员" },
        { value: "生产员", label: "生产员" },
      ],
      rules: {
        groupCode: [
          { required: true, message: "角色组编码不能为空", trigger: "change" },
          {
            pattern: /^[A-Z0-9]+$/,
            message: "角色组编码只能包含大写字母和数字",
            trigger: "change",
          },
        ],
        groupName: [
          { required: true, message: "角色组名称不能为空", trigger: "change" },
          {
            pattern: /^[^\s]+(\s+[^\s]+)*$/,
            message: "名称不能包含前后空格",
            trigger: "change",
          },
        ],
        roles: [
          { required: true, message: "请选择包含的角色", trigger: "change" },
          { type: 'array', min: 1, message: "至少选择一个角色", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择状态", trigger: "change" },
        ],
      },
    };
  },

  methods: {
    init() {
      this.loading = true;
      if (this.id) {
        // 模拟请求
        setTimeout(() => {
          this.loading = false;
          this.entity = {
            groupCode: "ERG1001",
            groupName: "测试电子签名角色组",
            roles: ["管理员", "操作员"],
            status: "启用",
            description: "这是一个测试电子签名角色组",
            creator: "admin",
            createTime: "2024-01-01 12:00:00",
            modifier: "admin",
            modifyTime: "2024-01-01 12:00:00",
          };
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },

    resetFormData() {
      this.entity = {
        groupCode: "",
        groupName: "",
        roles: [],
        status: "启用",
        description: "",
        creator: "",
        createTime: "",
        modifier: "",
        modifyTime: "",
      };
    },

    async save() {
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.groupName);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },

    inChange(e) {
      console.log(e.target.value);
    },
  },
};
</script>

<style lang="scss" scoped>
.card {
  background: #fff;
  padding: 24px;
  margin-bottom: 24px;
}
</style>